@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400;0,500;0,600;1,400;1,500;1,600&display=swap");
@mixin main-text {
  position: absolute;
  width: 100%;
  top: 0;
  text-align: center;
  height: 50%;
  top: 25%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html::-webkit-scrollbar {
  display: none;
}
body {
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  overflow-y: scroll;
}
header {
  z-index: 20;
  position: fixed;
  right: 0;
  width: 3vw;
  height: 100%;
  writing-mode: vertical-rl;
  font-family: "EB Garamond", serif;
  font-size: 2vmax;
  font-style: oblique;
  text-transform: capitalize;
  ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    li {
      cursor: pointer;
      &:first-child {
        transform: rotate(90deg);
      }
    }
  }
}
.container {
  height: 100vh;
  width: 100%;
}
.App {
  background-color: beige;
  display: grid;
  place-items: center;
  text-align: center;
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  .text {
    @include main-text;
  }
}
.section {
  position: absolute;
  width: 100vw;
  height: 100%;
  top: 0;
  right: 0;
  overflow: hidden;
}
.text {
  text-transform: capitalize;
  color: black;
  font-size: 6vmax;
  letter-spacing: -0.02em;
  font-weight: bold;
}
.miniWrap {
  display: flex;
  background-color: beige;
  .text {
    @include main-text;
  }
  .arrow {
    color: black;
    position: absolute;
    font-size: 6vmax;
    bottom: 0;
    right: 2%;
    animation: arrow 2s ease-in-out infinite;
  }
}
.bigWrap {
  z-index: 5;
  background-color: #ffe670;
  display: flex;
  .text {
    height: 100%;
    width: 40%;
    .text__inner {
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      font-size: 4.5vmax;
      height: 100%;
      width: 40%;
      position: absolute;
      span {
        margin: 0 auto;
        &:last-child {
          letter-spacing: 0.05em;
          margin: 5% 0 0 10%;
          font-size: 1.5vmax;
          color: white;
          -webkit-text-stroke: 1px black;
        }
      }
      p {
        overflow: hidden;
        font-style: italic;
        margin: 5% 0 0 10%;
        font-family: "EB Garamond", serif;
        font-size: 1.3vmax;
        letter-spacing: 0.03em;
        font-weight: 300;
      }
    }
  }
  .bigCanvas {
    z-index: 1;
  }
  .bigChairs {
    position: relative;
    width: 60%;
    height: 100%;
    .chair {
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      position: absolute;
      &::before {
        position: absolute;
        width: 40%;
        height: 80%;
        display: block;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        content: "";
        background-color: white;
        border-radius: 45%;
      }
    }
  }
  .button {
    z-index: 5;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 5%;
    width: 50%;
    height: 100%;
    position: absolute;
    right: 0;
    div {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(0, 0, 0, 0.2);
      border: none;
      border-radius: 50%;
      width: 10%;
      height: 10%;
      font-size: 5vmax;
      color: rgba(255, 255, 255, 0.7);
      cursor: pointer;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: -20%;
        height: 0;
      }
      &:hover,
      :focus,
      :active {
        background-color: rgba(0, 0, 0, 0.4);
        color: rgba(255, 255, 255, 1);
      }
    }
  }
}
.mouse {
  display: grid;
  place-items: center;
  align-items: end;
  position: absolute;
  width: 100%;
  height: 20%;
  font-size: 1.5vmax;
  animation: blink 2s linear infinite;
  svg {
    width: 100%;
    height: 30%;
    margin-bottom: 0.5%;
  }
}
@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes arrow {
  0% {
    right: 2%;
  }
  50% {
    right: 4%;
  }
  100% {
    right: 2%;
  }
}
